.loader-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  opacity: 0.5;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #555;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
